.letter-image {
	position: relative;
	top: 50%;
	left: 50%;
	width: 200px;
	height: 200px;
	-webkit-transform: translate(-50%, -50%);
	-moz-transform: translate(-50%, -50%);
	transform: translate(-50%, -50%);
 margin-top: 100px;
 cursor: pointer;
}

.animated-mail {
	position: absolute;
	height: 150px;
	width: 200px;
	-webkit-transition: .4s;
	-moz-transition: .4s;
	transition: .4s;

 transform: translateY(50px);
 -webkit-transform: translateY(50px);
 -moz-transform: translateY(50px);
	
	.body {
		position: absolute;
		bottom: 0;
		width: 0;
		height: 0;
		border-style: solid;
		border-width: 0 0 100px 200px;
		border-color: transparent transparent rgb(43,108,135) transparent;
		z-index: 2;
	}
	
	.top-fold {
		position: absolute;
		top: 50px;
		width: 0;
		height: 0;
		border-style: solid;
		border-width: 50px 100px 0 100px;
 transform-origin: 50% 0%;
 -moz-transform-origin: 50% 0%;
 -webkit-transform-origin: 50% 0%;
 border-color: rgb(23,88,115) transparent transparent transparent;

 transition: transform .4s, z-index .2s;
 transform: rotateX(180deg);
 -webkit-transition: transform .4s, z-index .2s;
 -webkit-transform: rotateX(180deg);
 -moz-transition: transform .4s, z-index .2s;
 -moz-transform: rotateX(180deg);
 z-index: 0;
	}
	
	.back-fold {
		position: absolute;
		bottom: 0;
		width: 200px;
		height: 100px;
		background: rgb(23,88,115);
		z-index: 0;
	}
	
	.left-fold {
		position: absolute;
		bottom: 0;
		width: 0;
		height: 0;
		border-style: solid;
		border-width: 50px 0 50px 100px;
		border-color: transparent transparent transparent rgb(33,98,125);
		z-index: 2;
	}
	
	.letter {
		left: 20px;
		bottom: 0px;
		position: absolute;
		width: 160px;
		height: 180px;
		background: white;
		z-index: 1;
		overflow: hidden;
		-webkit-transition: .4s .2s;
		-moz-transition: .4s .2s;
		transition: .4s .2s;
		
		.letter-border {
			height: 10px;
			width: 100%;
 background: repeating-linear-gradient(
 -45deg,
 rgb(23,88,115),
 rgb(23,88,115) 8px,
 transparent 8px,
 transparent 18px
 );
		}
		
		.letter-title {
			margin-top: 10px;
			margin-left: 5px;
			height: 10px;
			width: 40%;
			background: rgb(23,88,115);
		}
		.letter-context {
			margin-top: 10px;
			margin-left: 5px;
			height: 10px;
			width: 20%;
			background: rgb(23,88,115);
		}
		
		.letter-stamp {
			margin-top: 30px;
			margin-left: 120px;
			border-radius: 100%;
			height: 30px;
			width: 30px;
			background: rgb(23,88,115);
			opacity: 0.3;
		}
	}
}

.shadow {
	position: absolute;
	top: 200px;
	left: 50%;
 width: 250px;
	height: 30px;
	transition: .4s;
	transform: translateX(-50%);
	-webkit-transition: .4s;
	-webkit-transform: translateX(-50%);
	-moz-transition: .4s;
	-moz-transform: translateX(-50%);
	
	border-radius: 100%;
	background: radial-gradient(rgba(0,0,0,0.5), rgba(0,0,0,0.0), rgba(0,0,0,0.0));
}

.letter-image:active, .letter-image:hover{
 .animated-mail {
 transform: translateY(0px);
 -webkit-transform: translateY(0px);
 -moz-transform: translateY(0px);
 }
 
 .animated-mail .top-fold {
 -webkit-transition: transform .4s .4s, z-index .2s .4s;
		-moz-transition: transform .4s .4s, z-index .2s .4s;
		transition: transform .4s .4s, z-index .2s .4s;
 transform: rotateX(0deg);
 -webkit-transform: rotateX(0deg);
 -moz-transform: rotateX(0deg);
		z-index: 2;
 }
 
 .animated-mail .letter {
 height: 60px;
 }
 
 .shadow {
 width: 400px;
 }
}